import Star from "../../components/star/star.vue"

export default {
	data() {
		return {
			trailerInfo:{},
			plotPicsArray:[], //剧照
			directorArray:[] ,  //导演
			actorArray:[]
		}
	},
	onLoad(params) {
		// 获取上一个页面传入的参数 
		var trailerId = params.trailerId
		var serverUrl = this.serverUrl;
		
		// 请求预告片详细信息
		uni.request({
		    url: serverUrl+'/search/trailer'+trailerId,
			method:"POST",
			header:{
				'content-type':'application/x-www-form-urlencoded'
			},
			data:{
				qq:'1764194623'
			},
		    success: (res) => {
		        console.log(res.data);
				// 获取真实数据之前,判断状态是否为200
				if(res.data.status == 200){
					var trailerInfo= res.data.data
					this.trailerInfo = trailerInfo
					// 里面获得的剧照格式需要转换
					var plotPicsArray = JSON.parse(trailerInfo.pltoPics)
					this.plotPicsArray = plotPicsArray
				}
				}
			});
			
			// 请求导演信息 
			uni.request({
			    url: serverUrl+'/search/staff/'+trailerId + '/1',
				method:"POST",
				header:{
					'content-type':'application/x-www-form-urlencoded'
				},
				data:{
					qq:'1764194623'
				},
			    success: (res) => {
			        console.log(res.data);
					// 获取真实数据之前,判断状态是否为200
					if(res.data.status == 200){
						var directorArray= res.data.data
						this.directorArray = directorArray
					}
					}
				});
				
				// 请求演员信息
				uni.request({
				    url: serverUrl+'/search/staff/'+trailerId + '/2',
					method:"POST",
					header:{
						'content-type':'application/x-www-form-urlencoded'
					},
					data:{
						qq:'1764194623'
					},
				    success: (res) => {
				        console.log(res.data);
						// 获取真实数据之前,判断状态是否为200
						if(res.data.status == 200){
							var actorArray= res.data.data
							this.actorArray = actorArray
						}
						}
					});	
		
	},
	methods: {
		lookMe(){
			var me = this
			uni.previewImage({
				urls:me.plotPicsArray
			})
		}
	},
	components:{
		Star
	}
}
